"use client"

import { useState, useCallback } from "react"
import { Upload, Search, Filter, Wand2, Scissors, Trash2, ImageIcon, Copy } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { useToast } from "@/hooks/use-toast"
import { apiClient } from "@/lib/api-client"

interface Asset {
  fileId: string
  filename: string
  uploadedAt: Date
  thumbnail?: string
  type: "image" | "enhanced" | "cropped"
}

export default function AssetsPage() {
  const [assets, setAssets] = useState<Asset[]>([])
  const [selectedAsset, setSelectedAsset] = useState<Asset | null>(null)
  const [uploading, setUploading] = useState(false)
  const [processing, setProcessing] = useState(false)
  const [searchQuery, setSearchQuery] = useState("")
  const { toast } = useToast()

  const handleUpload = useCallback(
    async (files: FileList | null) => {
      if (!files || files.length === 0) return

      setUploading(true)
      const uploadPromises = Array.from(files).map(async (file) => {
        try {
          console.log("[v0] Uploading file:", file.name)
          const result = await apiClient.uploadAsset(file)
          console.log("[v0] Upload result:", result)

          const newAsset: Asset = {
            fileId: result.file_id,
            filename: result.filename,
            uploadedAt: new Date(),
            thumbnail: apiClient.getAssetUrl(result.file_id),
            type: "image",
          }

          toast({
            title: "Upload Successful",
            description: (
              <div className="space-y-2">
                <p>{file.name} uploaded</p>
                <div className="flex items-center gap-2 p-2 bg-muted rounded">
                  <span className="text-xs font-mono truncate">{result.file_id}</span>
                  <Button
                    size="sm"
                    variant="ghost"
                    className="h-6 w-6 p-0 shrink-0"
                    onClick={() => {
                      navigator.clipboard.writeText(result.file_id)
                      toast({ title: "Copied!", description: "File ID copied to clipboard" })
                    }}
                  >
                    <Copy className="h-3 w-3" />
                  </Button>
                </div>
              </div>
            ),
          })

          return newAsset
        } catch (error) {
          console.error("[v0] Upload failed:", error)
          toast({
            title: "Upload failed",
            description: `Failed to upload ${file.name}: ${error instanceof Error ? error.message : "Unknown error"}`,
            variant: "destructive",
          })
          return null
        }
      })

      const results = await Promise.all(uploadPromises)
      const successfulUploads = results.filter((asset): asset is Asset => asset !== null)

      setAssets((prev) => [...successfulUploads, ...prev])
      setUploading(false)
    },
    [toast],
  )

  const handleRemoveBackground = async (fileId: string) => {
    setProcessing(true)
    try {
      console.log("[v0] Removing background for:", fileId)
      const result = await apiClient.removeBackground(fileId)
      console.log("[v0] Remove BG result:", result)

      const newAsset: Asset = {
        fileId: result.new_file_id,
        filename: "Background Removed",
        uploadedAt: new Date(),
        thumbnail: apiClient.getAssetUrl(result.new_file_id),
        type: "enhanced",
      }

      setAssets((prev) => [newAsset, ...prev])

      toast({
        title: "Background removed",
        description: "New asset created with transparent background",
      })
    } catch (error) {
      toast({
        title: "Processing failed",
        description: "Could not remove background",
        variant: "destructive",
      })
    } finally {
      setProcessing(false)
      setSelectedAsset(null)
    }
  }

  const handleSmartEnhance = async (fileId: string) => {
    setProcessing(true)
    try {
      const result = await apiClient.smartEnhance(fileId, false)

      const newAsset: Asset = {
        fileId: result.new_file_id,
        filename: "Smart Enhanced",
        uploadedAt: new Date(),
        thumbnail: apiClient.getAssetUrl(result.new_file_id),
        type: "enhanced",
      }

      setAssets((prev) => [newAsset, ...prev])

      toast({
        title: "Enhancement complete",
        description: "Image enhanced with AI",
      })
    } catch (error) {
      toast({
        title: "Enhancement failed",
        description: "Could not enhance image",
        variant: "destructive",
      })
    } finally {
      setProcessing(false)
      setSelectedAsset(null)
    }
  }

  const handleSmartCrop = async (fileId: string, mode: "square" | "portrait" | "landscape") => {
    setProcessing(true)
    try {
      const result = await apiClient.smartCrop(fileId, mode)

      const newAsset: Asset = {
        fileId: result.new_file_id,
        filename: `Cropped (${mode})`,
        uploadedAt: new Date(),
        thumbnail: apiClient.getAssetUrl(result.new_file_id),
        type: "cropped",
      }

      setAssets((prev) => [newAsset, ...prev])

      toast({
        title: "Crop complete",
        description: `Image cropped to ${mode} format`,
      })
    } catch (error) {
      toast({
        title: "Crop failed",
        description: "Could not crop image",
        variant: "destructive",
      })
    } finally {
      setProcessing(false)
      setSelectedAsset(null)
    }
  }

  const handleDelete = async (fileId: string) => {
    try {
      await apiClient.deleteAsset(fileId)
      setAssets((prev) => prev.filter((asset) => asset.fileId !== fileId))
      setSelectedAsset(null)
      toast({
        title: "Asset deleted",
        description: "Asset removed from library",
      })
    } catch (error) {
      toast({
        title: "Delete failed",
        description: "Could not delete asset",
        variant: "destructive",
      })
    }
  }

  const filteredAssets = assets.filter((asset) => asset.filename.toLowerCase().includes(searchQuery.toLowerCase()))

  return (
    <div className="flex h-full flex-col">
      {/* Header */}
      <div className="border-b border-border bg-gradient-to-r from-card via-primary/5 to-card px-4 sm:px-6 md:px-8 py-4 sm:py-6">
        <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-3 sm:gap-0">
          <div>
            <h1 className="text-2xl sm:text-3xl font-bold tracking-tight">Asset Library</h1>
            <p className="mt-1 text-sm sm:text-base text-muted-foreground">
              Manage and enhance your product images with AI
            </p>
          </div>
          <label htmlFor="file-upload" className="w-full sm:w-auto">
            <Button size="lg" className="gap-2 gradient-primary w-full sm:w-auto" disabled={uploading}>
              <Upload className="h-4 w-4 sm:h-5 sm:w-5" />
              {uploading ? "Uploading..." : "Upload Assets"}
            </Button>
            <input
              id="file-upload"
              type="file"
              multiple
              accept="image/*"
              className="hidden"
              onChange={(e) => handleUpload(e.target.files)}
            />
          </label>
        </div>
      </div>

      {/* Toolbar */}
      <div className="border-b border-border bg-card px-4 sm:px-6 md:px-8 py-3 sm:py-4">
        <div className="flex flex-col sm:flex-row items-stretch sm:items-center gap-3 sm:gap-4">
          <div className="relative flex-1 max-w-full sm:max-w-md">
            <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
            <Input
              placeholder="Search assets..."
              className="pl-9"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </div>
          <Button variant="outline" size="sm" className="gap-2 bg-transparent">
            <Filter className="h-4 w-4" />
            Filter
          </Button>
        </div>
      </div>

      {/* Asset Grid */}
      <div className="flex-1 overflow-auto p-4 sm:p-6 md:p-8">
        {assets.length === 0 ? (
          <div className="flex h-full items-center justify-center px-4">
            <div className="text-center max-w-md">
              <div className="mx-auto mb-4 flex h-16 w-16 sm:h-20 sm:w-20 items-center justify-center rounded-full gradient-primary">
                <ImageIcon className="h-8 w-8 sm:h-10 sm:w-10 text-white" />
              </div>
              <h3 className="text-lg sm:text-xl font-semibold">No assets yet</h3>
              <p className="mt-2 text-sm sm:text-base text-muted-foreground text-balance">
                Upload your first product images to unlock AI-powered enhancements and smart cropping
              </p>
              <label htmlFor="file-upload-empty" className="block mt-4 sm:mt-6">
                <Button className="gradient-primary w-full sm:w-auto">Upload Your First Asset</Button>
                <input
                  id="file-upload-empty"
                  type="file"
                  multiple
                  accept="image/*"
                  className="hidden"
                  onChange={(e) => handleUpload(e.target.files)}
                />
              </label>
            </div>
          </div>
        ) : (
          <div className="grid gap-3 sm:gap-4 grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
            {filteredAssets.map((asset) => (
              <Card
                key={asset.fileId}
                className="group cursor-pointer overflow-hidden transition-all hover:border-primary hover:shadow-lg"
                onClick={() => setSelectedAsset(asset)}
              >
                <div className="relative aspect-square overflow-hidden bg-muted">
                  <img
                    src={asset.thumbnail || "/placeholder.svg"}
                    alt={asset.filename}
                    className="h-full w-full object-cover transition-transform group-hover:scale-110"
                  />
                  {asset.type !== "image" && (
                    <Badge className="absolute top-2 right-2 gradient-accent" variant="secondary">
                      {asset.type}
                    </Badge>
                  )}
                  <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent opacity-0 group-hover:opacity-100 transition-opacity" />
                </div>
                <CardContent className="p-4">
                  <p className="truncate text-sm font-medium">{asset.filename}</p>
                  <p className="mt-1 text-xs sm:text-sm text-muted-foreground">
                    {asset.uploadedAt.toLocaleDateString()}
                  </p>
                </CardContent>
              </Card>
            ))}
          </div>
        )}
      </div>

      {/* Asset Detail Dialog */}
      <Dialog open={!!selectedAsset} onOpenChange={() => setSelectedAsset(null)}>
        <DialogContent className="max-w-[95vw] sm:max-w-3xl">
          <DialogHeader>
            <DialogTitle>{selectedAsset?.filename}</DialogTitle>
            <DialogDescription>
              <div className="space-y-2">
                <p>Uploaded {selectedAsset?.uploadedAt.toLocaleDateString()}</p>
                <div className="flex items-center gap-2 p-2 bg-muted rounded mt-2">
                  <span className="text-xs font-mono truncate flex-1">{selectedAsset?.fileId}</span>
                  <Button
                    size="sm"
                    variant="ghost"
                    className="h-6 w-6 p-0"
                    onClick={() => {
                      if (selectedAsset) {
                        navigator.clipboard.writeText(selectedAsset.fileId)
                        toast({ title: "Copied!", description: "File ID copied to clipboard" })
                      }
                    }}
                  >
                    <Copy className="h-3 w-3" />
                  </Button>
                </div>
              </div>
            </DialogDescription>
          </DialogHeader>

          <div className="my-4">
            <div className="aspect-video overflow-hidden rounded-lg bg-muted">
              <img
                src={selectedAsset?.thumbnail || "/placeholder.svg"}
                alt={selectedAsset?.filename}
                className="h-full w-full object-contain"
              />
            </div>
          </div>

          <DialogFooter className="flex-col gap-2 sm:flex-row">
            <div className="flex flex-1 flex-wrap gap-2">
              <Button
                variant="outline"
                size="sm"
                className="gap-2 bg-transparent"
                onClick={() => selectedAsset && handleRemoveBackground(selectedAsset.fileId)}
                disabled={processing}
              >
                <Scissors className="h-4 w-4" />
                Remove BG
              </Button>
              <Button
                variant="outline"
                size="sm"
                className="gap-2 bg-transparent"
                onClick={() => selectedAsset && handleSmartEnhance(selectedAsset.fileId)}
                disabled={processing}
              >
                <Wand2 className="h-4 w-4" />
                Enhance
              </Button>
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="outline" size="sm" className="gap-2 bg-transparent" disabled={processing}>
                    <Scissors className="h-4 w-4" />
                    Smart Crop
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent>
                  <DropdownMenuLabel>Crop Format</DropdownMenuLabel>
                  <DropdownMenuSeparator />
                  <DropdownMenuItem onClick={() => selectedAsset && handleSmartCrop(selectedAsset.fileId, "square")}>
                    Square (1:1)
                  </DropdownMenuItem>
                  <DropdownMenuItem onClick={() => selectedAsset && handleSmartCrop(selectedAsset.fileId, "portrait")}>
                    Portrait (4:5)
                  </DropdownMenuItem>
                  <DropdownMenuItem onClick={() => selectedAsset && handleSmartCrop(selectedAsset.fileId, "landscape")}>
                    Landscape (16:9)
                  </DropdownMenuItem>
                </DropdownMenuContent>
              </DropdownMenu>
            </div>
            <div className="flex gap-2 w-full sm:w-auto">
              <Button
                variant="destructive"
                size="sm"
                className="gap-2 flex-1 sm:flex-initial"
                onClick={() => selectedAsset && handleDelete(selectedAsset.fileId)}
              >
                <Trash2 className="h-4 w-4" />
                Delete
              </Button>
              <Button
                variant="ghost"
                size="sm"
                className="flex-1 sm:flex-initial"
                onClick={() => setSelectedAsset(null)}
              >
                Close
              </Button>
            </div>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}
